 .slider {
	background: transparent;
	height: 200px;
	margin: auto;
	overflow: hidden;
	position: relative;
	width: 90%;
  }
  
  .slider::before,
  .slider::after {
	background: transparent;
	content: "";
	height: 200px;
	position: absolute;
	width: 200px;
	z-index: 2;
  }
  
  .slider::after {
	right: 0;
	top: 0;
	transform: rotateZ(180deg);
  }
  
  .slider::before {
	left: 0;
	top: 0;
  }
  
  .slide-track {
	animation: scroll 30s linear infinite;
 	display: flex; 
	width: calc(250px * 28);
  }
  
  .slide {
	height: 100px;
	width: 250px;
	align-items: center;
	justify-content: center;
	
  }
  
  @keyframes scroll {
	0% {
	  transform: translateX(0);
	}
	100% {
	  transform: translateX(calc(-250px * 14));
	}
  }
  
	
	  